    url('asset/font/my-resume.woff?z785lj') format('woff'),
    url('asset/font/my-resume.svg?z785lj#my-resume') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="my-resume__icon--"], [class*=" my-resume__icon--"] {
